@import url(https://fonts.googleapis.com/css?family=Quicksand);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand);

/* ====== | CSS Reset | ====== */

html, body, div, main, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, button, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, input, textarea, select,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video,
::before, ::after {
	box-sizing: border-box;
	margin: 0; 
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	text-decoration: none;
}

/* HTML5 - Older browser support
====================================================================== */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

/* BASE 10
====================================================================== */
html { font-size: 62.5%; }

/* BODY
====================================================================== */
body { line-height: 1; }

/* LISTS - Clear bullets
====================================================================== */
ol, ul, li { list-style: none; }


/* QUOTES
====================================================================== */
blockquote, q { quotes: none; }
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

/* TABLES
====================================================================== */
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* IMAGES
====================================================================== */
img {
	display: block;
	width: 100%;
}
.App {
    display: flex;
    flex-direction: column;
    font-size: 1.8rem;
    font-family: 'Quicksand';
    color: #fff;
    background-color: #171717;
}

.search {
    display: inline-flex;
    position: relative;
}

.search input {
    width: 400px;
    padding: 10px;
    border: none;
    border-radius: 10px;
    font-size: 1.8rem;
    color: #fff;
    background-color: rgba(34, 63, 136, 0.5);
}

.search input::-webkit-input-placeholder {
    color: #fff;
}

.search input:-ms-input-placeholder {
    color: #fff;
}

.search input::-ms-input-placeholder {
    color: #fff;
}

.search input::placeholder {
    color: #fff;
}

.search-glass {
    position: absolute;
    right: 0;
    width: 20px;
    height: 20px;
    margin: 1rem;
}
.search-glass:hover {
    cursor: pointer;
}

.current-forecast-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 1rem;
    font-size: 2.8rem;
    width: 1600px;
    border-radius: 10px;
    background-color: rgba(34, 63, 136, 0.5);
}

/* 
Date + Location 
*/
.date-location {
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 100%;
    justify-content: center;
    padding: 0 1rem 1rem 1rem;
}
.date-location::before {
    content: '';
    display: block;
    height: 30px;
    width: 30px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.date, .location {
    margin: 1rem;
    font-size: 1.8rem;
}


/* 
Forecast Temps
*/
.forecast-temps {
    position: relative;
    height: 100%;
    display: flex;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(50% - 4rem);
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    padding: 2rem;
}

.weather .forecast-icon {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    height: 350px;
    width: 350px;
}

.current-temp {
    font-size: 12rem;   
    margin-left: 8rem; 
}

.high-low {
    display: flex;
    flex-direction: row;
    margin: 6rem;
}
.high-low .max-temp {
    margin-right: 2rem;
}

/* 
Forecast info
*/
.forecast-info {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(50% - 4rem);
    justify-content: center;
    padding: 2rem;
}

.forecast-info p {
    position: absolute;
    top: 10%;
    text-align: center;
}

.forecast-misc {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
}

.misc-forecast-icons {
    display: inline-block;
    height: 100px;
    width: 100px;    
}

.humidity, .wind, .sunrise, .sunset {
    text-align: center;
    padding: 4rem;
}

.forecast-snippet {
    display: flex;
    justify-content: center;
    font-size: 2.4rem;
}

.forecast-tab {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    background-color: rgba(11, 13, 95, 0.5);
}

.forecast-tab:hover {
    cursor: pointer;
    background-color: rgba(11, 13, 95, 0);
}

.forecast-tabs .active {
    background-color: rgba(11, 13, 95, 0);
}

.forecast-temp {
    font-size: 6rem;
    padding-top: 2rem;
}


.forecast-weather .forecast-icon {
    display: block;
    height: 300px;
    width: 300px;
}

.weather-sect-container {
    display: flex;
    flex-direction: column;
    flex-grow: 2;
    height: 90%;
    background-image: url(/portv/static/media/clear_bg.21f9cf3d.jpg);
    background-repeat: no-repeat;
    background-size: cover;   
    text-shadow: 2px 2px #000;
}

.top-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 2;
    padding: 10px;
}

.top-section .forecast-search {
    display: flex;
}

.current-weather {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}

.forecast-tabs {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    flex-direction: row;
    margin-top: 2rem;
    box-shadow: 0px 20px 10px rgba(22, 5, 44, 0.5);
}

.forecast-tab {
    border-right: 2px solid #fff;
    margin-bottom: auto;
    height: 400px;
    flex-grow: 1;
}

.null-data {
    padding: 1rem;
    margin: 0 auto;
}


.map-container {
    margin: 2rem;
}
.info-container {
    position: relative;
    width: 800px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start;
}
.info-container::after {
    content: ''; 
    display: block;
    width: 500px;
    height: 400px;
    background-image: url(/portv/static/media/biker.442ac055.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.info-container h2 {
    display: flex;  
    flex-basis: 100%;
    font-family: 'Black Ops One';
    font-size: 5rem;
    text-decoration: underline;
    padding: 2rem;
}

.info-container p {
    display: flex;
    padding: 2rem;
    line-height: 1.7;
}
.info-container p:first-of-type {
    font-size: 2.8rem;
    padding-top: 5rem;
}
.info-container p:last-of-type {
    font-size: 1.6rem;
    font-style: italic;
}

