.map-sect-container {
    position: relative;
    margin: 0 auto;
    padding: 5rem 2rem 2rem 2rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.map-sect-container::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    height: 100px;
    width: 800px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.map-info {
    flex-grow: 2;
}

.footer-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 10rem;
    width: 100%;
    background-color: #000;
    color: #fff;
    text-align: center;
}
.datapage-container {
    display: flex;
    flex-direction: column;
    font-size: 1.8rem;
    font-family: 'Quicksand';
    color: #fff;
    background-color: #170952;
}


.homepage {
    position: relative;
    height: 100vh;
    width: 100vw;
    background-image: url(/portv/static/media/homepage_bg.0216fdc6.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

.homepage-search {
    position: absolute;
    top: 15rem;   
    left: 50%;
    transform: translate(-50%, 0);
}

.logo {
    display: block;
    position: absolute;
    height: 120px;
    width: 150px;
    top: 0;
    left: 0;
}

/* Tablet Responsiveness */
@media (max-width: 1200px) {
    .homepage {
        background-image: url(/portv/static/media/homepage_bg_tablet.15d71c9b.jpg);
    }
}

/* Mobile Responsiveness */
@media (max-width: 800px) {
    .homepage {
        background-image: url(/portv/static/media/homepage_bg_mobile.23423f1b.jpg);
    }
}
@media (max-width: 500px) {
    .homepage {
        background-image: none;
        background-color: #242424;
    }

    .homepage-search .search input{
        width: 300px;
    }
}
@media (max-width: 350px) {
    .homepage-search .search input{
        width: 200px;
    }
}
